.chat_div {
  position: relative;
  height: 100%;
  width: 100%;
  box-shadow: #00000017 0px 3px 12px;
  border-radius: 18px;
  background-color: #101010;
  .chat_content2 {
    display: flex;
    border-radius: 10px;
    margin-left: 13px;
    justify-content: space-between;
    padding: 7px 15px;
    width: auto;
    background: #d0c7df;
    margin-top: 10px;
    word-break: break-word;
    flex-direction: column;
    p {
      color: #1e836f;
    }
    span {
      color: #151515;
    }
  }
  .time_stamp_chat2 {
    font-family: "poppins";
    font-weight: 400;
    font-size: 10px;
    position: absolute;
    top: 5px;
    right: 10px;
    color: #ffff;
  }

  .call_details {
    display: flex;
    flex-direction: column;

    text-align: center;
    width: 100%;
    padding: 10px;
    margin-top: 15px;
    border-bottom: 0.5px solid #c0c0c0;
    opacity: 0.8;
    margin-bottom: 20px;
  }
  .call_details_p {
    font-size: 14px;
    font-family: "poppins";
    display: flex;
    justify-content: center;
    font-weight: 500;
    color: #ffff;
    margin-bottom: 0px;
    margin-top: 0px;
    img {
      // opacity: 0.8;
      width: 20px;
    }
  }
  .reply2mes {
    color: #333232 !important;
    font-family: "poppins";
    font-weight: 500;
    font-size: 16px;
    margin-bottom: 0px;
  }
  .down_ref {
    margin-right: 10px;
    font-family: "poppins";
  }
  .file_content {
    display: flex;
    flex-direction: column;
    p {
      font-size: 15px;
      font-weight: 600;
      font-family: "Poppins";
      margin-left: 14px;
      margin-bottom: 0px;
      color: #209f85;
      word-break: break-word;
    }
    span {
      color: #ffff;
      word-break: break-word;
    }
  }
  .reply_file_content {
    display: flex;
    flex-direction: column;
    margin-left: 20px;
    p {
      font-size: 15px;
      font-weight: 600;
      font-family: "Poppins";
      margin-bottom: 0px;
      color: #209f85;
    }
    span {
      color: #ffff;
      word-break: break-word;
    }
  }
  .href_a {
    font-family: "Poppins";
    font-size: 16px;
    font-weight: 500;
    display: flex;
    align-items: center;
  }
  .chatHeader {
    height: auto;
    padding: 15px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    background: #303030;
    position: relative;
    top: 0;
    z-index: 99;
    border-radius: 10px 10px 0 0px;
  }
  .chat-close {
    position: absolute;
    top: 12px;
    right: 12px;
    color: #ffffffc4;
    font-size: 18px;
    cursor: pointer;
  }
  .Qoute {
    font-family: "poppins";
    font-size: 17px;
    font-weight: 500;
    margin-bottom: 0px;
    color: #ffffff;
    span {
      float: right;
      margin-top: 5px;
      color: #d3d3d388;
      font-weight: 600;
      font-size: 22px;
    }
  }
  .typingindic {
    position: absolute;
    letter-spacing: 0.8px;
    top: 10%;
    margin-top: 10px;
    font-family: "Poppins";
    font-size: 15px;
    display: flex;
    padding-left: 15px;
    padding-right: 15px;
    color: #000000;
    font-weight: 400;
    padding-top: 2px;
    background: #ffffffbb;
    box-shadow: 0 8px 32px 0 #1f26875e;
    border-radius: 25px;
    .chat-typer-name {
      max-width: 12ch;
      width: auto;
      text-overflow: ellipsis;
      overflow: hidden;
    }
  }
  .chat_body {
    padding: 10px;
    overflow: auto;
    height: 72% !important;
  }
  .messageSeen {
    float: right;
    margin-right: 3px;
    margin-top: -35px;
    svg {
      color: #1e836f;
    }
  }
  #messageBox {
    // height: 54vh;
  }
  .seen_icon {
    font-size: 16px;
    color: black;
  }
  .left_chat {
    position: relative;
    background-color: #303030;
    justify-content: space-between;
    margin-top: 12px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
    width: 88%;
    box-shadow: #00000026 0px 5px 15px 0px;
    border-radius: 0px 13px 13px 13px;
    &:hover {
      .chat_right {
        display: block;
      }
    }
  }
  .chat_right {
    position: absolute;
    display: none;
    bottom: 5px;
    right: 8px;
  }
  .left_chat_left {
    display: flex;
    flex-direction: row;
    align-items: unset;
  }

  .right_chat {
    position: relative;
    background: #303030;
    justify-content: space-between;
    margin-left: 43px;
    margin-top: 10px;
    margin-bottom: 10px;
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 10px;
    width: 88%;
    box-shadow: #00000026 0px 5px 15px 0px;
    border-radius: 13px 13px 0 13px;
  }
  .right_chat_left {
    display: flex;
    flex-direction: row;
    align-items: center;
  }

  .user_icon {
    height: 50px;
    width: 50px;
    border-radius: 50%;
    box-shadow: #00000026 0px 5px 15px 0px;
    display: flex;
    justify-content: center;
    background-color: #ccf2ff;
    font-size: 32px;
    font-weight: 600;
    text-transform: capitalize;
    margin-top: auto;
    color: #0086b3;
  }

  .user_icon2 {
    height: 50px;
    width: 50px;
    border-radius: 50%;
    box-shadow: #00000026 0px 5px 15px 0px;
    display: flex;
    justify-content: center;
    background-color: #e4d2f9;
    font-size: 32px;
    font-weight: 600;
    text-transform: capitalize;
    margin-top: auto;
    color: #5e16b6;
  }

  .user_icon_ .chat_img {
    height: 50px;
    width: 50px;
    border-radius: 10px;
    box-shadow: #00000026 0px 5px 15px 0px;
  }
  .chat_content {
    margin-left: 20px;
    word-break: break-word;
  }
  .chat_content {
    p {
      font-family: "poppins";
      font-weight: 500;
      font-size: 16px;
      margin-bottom: 0px;
      color: #209f85;
    }
  }
  .chat_content span {
    font-family: "poppins";
    font-weight: 400;
    font-size: 15px;
    color: #ffffff;
    word-break: break-word;
  }
  .text_input_box {
    width: 320px;
    font-size: 18px;
    background-color: #101010;
    padding-top: 8px;
    max-height: 40px;
    font-family: "Poppins";
    font-weight: 450;
    border-bottom: 1px solid #48534d;
    border-radius: 10px 10px 0px 0px;
    color: #f1f1f1;
    overflow: auto;
    outline: none;
  }

  .text_input {
    position: absolute;
    bottom: 1px;
    width: 100%;
    display: flex;
    flex-direction: column;
    padding: 2px;
    height: auto;
    background-color: #151515;
  }
  .input_send {
    display: flex;
    flex-direction: row;
    padding: 7px;
    margin-right: 0px;
    margin-left: 0px;
  }
  .file_editor {
    display: flex;
    flex-direction: row;
    justify-content: left;
    padding-left: 10px;
  }
  .chat_header_close2 {
    margin-left: 10px;
    display: flex;
  }
  .sendbtn {
    font-size: 25px;
    background-color: #209f85;
    color: white;
    border: 2px solid #209f85;
    height: 40px;
    padding: 7px;
    padding-left: 10px;
    width: 80px;
    cursor: pointer;
    border-radius: 9px;
  }
  .sendbtn:hover {
    opacity: 0.8;
  }
  .text_input {
    input {
      border-radius: 25px;
      width: 380px;
      border: 1.8px solid #209f85;
      outline: none;
      padding: 5px;
      height: 41px;
      font-weight: normal;
      font-size: 16px;
      caret-color: #209f85;
    }
  }
  .text_input_b {
    input {
      border-radius: 25px;
      font-weight: bold;
      width: 380px;
      border: 1.8px solid #209f85;
      outline: none;
      padding: 5px;
      height: 41px;
      font-size: 16px;
      caret-color: #209f85;
    }
  }
  #fileUploader {
    opacity: 0;
    position: absolute;
    z-index: -1;
  }
  #double_check {
    font-size: 20px;
    display: flex;
    justify-content: center;
    color: green;
  }
  .tickspan {
    margin-top: -40px;
    position: absolute;
    left: 85%;
  }
  .attach_pin {
    z-index: 1;
    margin-top: 7px;
    font-size: 26px;
    color: #757575;
  }
  .file_append {
    width: 280px;
    height: auto;
    margin-top: -55px;
    margin-bottom: 5px;
    padding-left: 20px;
    padding-bottom: 8px;
    border-radius: 4px;
    margin-left: 4px;
    padding-top: 2px;
    padding-right: 20px;
    background-color: #eaecef;
    color: black;
    display: flex;
    justify-content: space-between;
  }
  .file_icon {
    font-size: 15px;
  }
  .file_append_file {
    font-family: "poppins";
    font-size: 14.5px;
    font-weight: 400;
  }
  .close_file_icon {
    font-size: 25px;
    cursor: pointer;
    display: flex;
    justify-content: center;
    margin-top: 10px;
  }
  .emoji_picker {
    font-size: 34px;
    margin-left: 10px;
    margin-top: 3px;
    cursor: pointer;
    margin-right: 5px;
    padding-right: 5px;
  }

  .emoji_picker_chat {
    z-index: 1;
    margin-top: 7px;
    font-size: 26px;
    color: #757575;
  }
  .bold_text {
    font-size: 40px;
    width: 32px;
    height: 42px;
    color: #757575;
    display: flex;
    cursor: pointer;
    margin-right: 6px;
    outline: none;
    justify-content: center;
    margin-left: 5px;
    border: none;
    border-radius: 8px;
    background-color: transparent;
  }
  .filehref {
    font-family: "poppins";
    padding: 10px;
    border-radius: 12px;
    font-size: 15px;
    background-color: #c4e2f3;
    color: black;
  }
  .bold_icon {
    margin-top: 3px;
    cursor: pointer;
    font-size: 37px;
  }
  .user_mention {
    width: 60%;
    overflow: auto;
    box-shadow: #0000001a 0px 4px 12px;
    border-radius: 12px;
    padding: 15px;
    position: absolute;
    background-color: #202020;
    height: auto;
    bottom: 92px;
  }
  .user_mention_list {
    box-shadow: #63636333 0px 2px 8px 0px;
    background-color: #181818;
    color: #ffffff;
    text-overflow: ellipsis;
    padding: 4px;
    text-align: center;
    border-radius: 10px;
    margin-bottom: 6px;
    font-size: 17px;
    font-weight: 500;
    text-transform: Capitalize;
    letter-spacing: 1px;
    cursor: pointer;
  }
  .user_mention_sel {
    width: 60%;
    overflow: auto;
    box-shadow: #0000001a 0px 4px 12px;
    border-radius: 12px;
    position: absolute;
    background-color: #303030;
    color: #ffffff;
    height: auto;
    bottom: 92px;
    padding-bottom: 5px;
    padding-left: 10px;
    padding-right: 10px;
    padding-top: 10px;
    display: flex;
    justify-content: space-between;
  }

  .reply_content_sel {
    width: 60%;
    overflow: auto;
    box-shadow: #0000001a 0px 4px 12px;
    border-radius: 12px;
    position: absolute;
    background-color: #303030;
    color: #ffffff;
    height: auto;
    bottom: 92px;
    padding-bottom: 10px;
    padding-left: 10px;
    padding-right: 10px;
    padding-top: 10px;
    display: flex;
    justify-content: space-between;
    .reply_chat_content {
      p {
        font-family: "poppins";
        font-weight: 500;
        font-size: 16px;
        margin-bottom: 0px;
        color: #209f85;
      }

      span {
        font-family: "poppins";
        font-weight: 400;
        font-size: 14px;
        color: #ffffff;
        display: block;
        text-overflow: ellipsis;
        white-space: nowrap;
        max-width: 16ch;
        width: auto;
        overflow: hidden;
      }
    }
  }

  .reply_sel_msg {
    padding-bottom: 5px;
    padding-left: 10px;
    padding-right: 10px;
    padding-top: 10px;
    display: flex;
    justify-content: space-between;
  }

  .mentioned_user {
    font-size: 17px;
    font-weight: 500;
  }
  .reply_btn {
    width: 20px;
    cursor: pointer;
  }
  .replyFileName {
    font-family: "Poppins";
    font-size: 16px;
    font-weight: 500;
    display: flex;
    align-items: center;
    color: #ffffff;
  }
}

.ant_tooltip_chat {
  .ant-tooltip-inner {
    min-width: 38px;
    min-height: 20px;
    padding: 6px 8px;
    color: #008000;
    text-align: left;

    text-decoration: none;
    word-wrap: break-word;
    border-radius: 7px;
    box-shadow: #0e090959 0px 5px 15px;
  }

  .ant-tooltip-placement-top .ant-tooltip-arrow {
    left: 50%;
    display: none;
  }
}

.insert_link_main {
  .insert_buttons {
    display: flex;
    justify-content: space-evenly;
    margin-right: 10px;
    margin-top: 5px;
    margin-left: 70px;
  }
  .insert_cbtn {
    color: black;
    font-size: 13px;
    width: 80px;
    line-height: 1.1;
    background-color: #ffffff;
    height: 28px;
    border-radius: 9px;
    border: 1px solid #209f85;
  }
  .insert_cbtn:hover {
    background-color: #ffffff;
    border: 1px solid #209f85;
    color: black;
  }
  .insert_ibtn {
    color: #ffffff;
    border-radius: 9px;
    border: 1px solid #ffffff;
    font-size: 13px;
    background-color: #209f85;
    line-height: 1.1;
    height: 28px;
    width: 80px;
  }
  .insert_ibtn:hover {
    background-color: #ffffff;
    color: #209f85;
    border: 1px solid #209f85;
  }
  .insert_link_header {
    margin-bottom: 0px;
    font-weight: 700;
    font-size: 18px;
    border-bottom: 1px solid #f0f0d9;
  }

  .insert_start {
    padding: 5px;
  }

  .insert_address {
    padding: 5px;
    input {
      margin-bottom: 0px;
      margin-top: 5px;
    }
  }
  .insert_text {
    padding: 5px;
    input {
      margin-bottom: 0px;
      margin-top: 5px;
    }
  }
}
