.videoUploader {
  .label {
    font-weight: 500 !important;
    font-size: 18px !important;
  }

  .form-control-lg {
    border-radius: 7px !important;
    border-color: #219f86 !important;
  }

  .invalid-field {
    margin-top: 10px !important;
    font-family: "Poppins";
    font-weight: 400;
    color: #af0405;
    padding-left: 10px;
    font-size: 13px;
  }

  .form-control:focus {
    border-color: transparent !important;
    box-shadow: 0 0 0 0.2rem #219f8634 !important;
  }

  .form-control.is-invalid {
    border-color: #219f86 !important;
  }

  .form-control {
    border-radius: 10px !important;
    width: 30px !important;
    height: 30px;
    /* cursor: pointer; */
    margin-left: 5px;
    /* cursor: pointer;*/
  }

  #RefDocumentLink {
    opacity: 0;
  }
  .form-group {
    /* margin-bottom: 1rem; */
    opacity: 0;
    position: absolute;
    margin-left: -9px;
    margin-top: 3px;
    position: absolute;
    z-index: 1;
  }
}
