@media only screen and (max-width: 600px) {
  .video-call-container {
    .videoPlayers {
      display: flex;
      flex-wrap: wrap;
    }
    .screen-share-video-player {
      width: 100%;
      height: 50%;
      .screenshare-video-content {
        width: 100%;
        height: 100%;
        video {
          width: 100%;
          height: 100%;
          object-fit: fill;
          background: #101010;
        }
      }
    }
    .video_func_div {
      flex-wrap: wrap;
      justify-content: center;

      padding: 10px 18px;
    }
    .leave-btn {
      display: none;
    }

    .video_funcs {
      padding: 13px 13px;
    }

    .func_icon {
      font-size: 18px;
    }
    .video_funcs2 {
      padding: 9px 18px;
      margin-left: 0;
      border-radius: 0 5px 5px 0;
      margin-top: 0px;
    }

    .video_funcs_corner {
      border-radius: 0px;
    }
    .func_icon2 {
      font-size: 24px;
      font-weight: 800;
      color: #ffffff;
    }

    .video_consultation_main .local-video-user-details .local-video-username {
      font-family: "Poppins";
      font-size: 0.6rem;
      font-weight: 500;
      padding: 2px 2px;
      letter-spacing: 1px;
      margin-right: 5px;
    }

    .handrise-subicon-localvideo {
      font-size: 12px;
    }

    .antsdk-remote-video-username {
      font-family: "Poppins";
      font-size: 0.4rem;
      font-weight: 500;
      letter-spacing: 0.3px;
      width: 12ch;
      text-overflow: ellipsis;
      overflow: hidden;

      white-space: nowrap;

      padding: 1px 5px;
    }
    .antsdk-remote-video-mic-icon {
      align-items: center;

      display: flex;

      svg {
        width: 10px;
        height: 10px;
      }
    }

    .antsdk-remote-video-handraise-icon {
      svg {
        width: 13px;
        height: 13px;
        margin-top: -6px;

        margin-left: 5px;
      }
    }

    .side_div_main {
      margin-left: 0px;

      width: 89%;
      position: absolute;

      height: 81.5vh;

      box-shadow: #63636333 0px 2px 8px 0px;
      overflow: hidden;
      z-index: 999;
    }
  }

  // Joining Modal Query

  .joinModel {
    .modelLocalVideoPlayer {
      margin: 20px;
      width: 70%;
      video {
        width: 100%;
        height: 100%;
        object-fit: fill;
        border-radius: 10px;
        background: #3c3a3a;
      }
    }
    .joining-localVideoControls {
      .joining-form {
        margin-left: 20px;
      }
      .joining-username {
        border-radius: 7px;
        width: 100%;
        border: none;
      }
      .join-label {
        font-size: 14px;
      }
      .joinbtn {
        width: 100%;
        margin-right: 10px;
        padding: 7px 10px;
      height: fit-content;
        border-radius: 7px;
        font-size: 16px;
        background-color: #37a991;
        border: none;
        letter-spacing: 1px;
      }

      .cancelbtn {
        margin-right: 10px;
        padding: 7px 10px;
     height: fit-content;
        border-radius: 7px;
        font-size: 16px;
        border-color: #37a991;
        color: #37a991;
      }
    }


  }

  // Chat Media Query

  .chat_div {
    border-radius: 12px;
  }

  
  .CameraAccessModel{
    display :flex;
    flex-direction: column;
  
    .camera-access-block{
      text-align: center;
  font-size: 16px ;
  font-weight:400;
  color: #ffffff;
    }
    .camera-access-allow{
      text-align: center;
      font-size: 14px;
      font-weight:600;
      color: #ffffff;
    }
  }
}
@media only screen and (min-width: 1201px) and (max-width: 1400px) {
  .video-call-container {
    .side_div_main {
      margin-left: 0px;
      width: 40%;
      height: 81.5vh;
      box-shadow: #63636333 0px 2px 8px 0px;
      overflow: hidden;
      z-index: 999;
    }
  }
}
@media only screen and (min-width: 1001px) and (max-width: 1200px) {
  .video-call-container {
    .side_div_main {
      width: 50%;
    }
  }
}
@media only screen and (min-width: 901px) and (max-width: 1000px) {
  .video-call-container {
    .side_div_main {
      width: 55%;
    }
  }
}
@media only screen and (min-width: 801px) and (max-width: 900px) {
  .video-call-container {
    .side_div_main {
      width: 65%;
    }
  }
}
@media only screen and (min-width: 601px) and (max-width: 800px) {
  .video-call-container {
    .side_div_main {
      width: 100%;
    }
  }
}
