.participant-list-all {
  width: 100%;
  height: 100%;
  font-family: "poppins";
  background-color: #151515;
  overflow: hidden;
  .participant-heading {
    font-size: 20px;
    font-weight: 500;
    margin: 0px;
    padding: 15px 0px;
    text-align: center;
    background-color: #303030;
    color: #ffffff;
    position: relative;
  }
  .participant-close {
    position: absolute;
    top: 14px;
    right: 12px;
    color: #ffffffc4;
    font-size: 18px;
    cursor: pointer;
  }
  .list-wrapper {
    height: 77vh;
  }
  .list {
    border-radius: 2px;
    list-style: none;
    padding: 10px 20px;
    overflow-y: scroll;
    height: 100%;
  }

  .list-item {
    display: flex;
    padding-bottom: 10px;
    padding-top: 10px;
    border-bottom: 1px solid #a8a8a83b;
    &:last-child {
      border-bottom: none;
    }

    .user-name-letter {
      text-transform: uppercase;
      border-radius: 50%;
      padding: 5px 4px;
      width: 50px;
      opacity: 0.8;
      font-size: 1.6rem;
      background-color: #c0c0c0;
      border: 2px solid #fdfdfd;
      color: black;
      text-align: center;
    }
    .list-item-content {
      margin-left: 20px;
      display: flex;
      align-items: center;
      .list-name {
        margin-top: 5px;
        font-size: 18px;
        word-break: break-all;
        color: #ffffff;
      }
    }
  }
}
