.overallbody {
  width: 100%;
  height: 100vh;
  background-color: #151515;
}

.ant-tooltip-inner {
  border-radius: 12px !important;
  background-color: transparent !important;
  padding: 0 !important;
  color: #0f0f0f;
}

.ant-notify-class {
  font-size: 18px;
  font-weight: 600;
  color: #1a1919ee;
}
.video-call-container {
  display: flex;
  flex-direction: column;
  background-color: #151515;
  .count_span {
    position: absolute;
    top: -8px;
    right: -3px;
    padding: 3px 2px;
    background: #5df88b;
    width: 22px;
    font-size: 12px;
    border-radius: 50%;
    text-align: center;
    font-family: "Poppins";
    font-weight: 500;
    z-index: 9999;
  }
  .video_func_div {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: space-between;
    // position: sticky;
    // top: 0; //
    background: transparent;
    z-index: 999;
    padding: 15px 20px 5px 20px;
    transition: 0.3s all ease-in-out;
  }
  .video_func_elements {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    transition: 0.3s all ease-in-out;
    border-radius: 12px;
  }
  .first-options-btn {
    border-radius: 5px 0 0 5px;
  }
  .video_funcs_corner {
    border-radius: 0 5px 5px 0;
  }
  .video_funcs:hover {
    background-color: #303030;
    border-radius: 5px;
  }

  .video_funcs {
    background: #0f0f0f;
    padding: 10px 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 0px;
    margin-right: 0px;
    cursor: pointer;
    position: relative;
  }
  .func_icon {
    font-size: 20px;
    font-weight: 800;
    color: #ffff;
  }
  .leave-btn {
    margin-left: 10px;
  }
  .video_funcs2 {
    border-radius: 5px;
    display: flex;
    justify-content: center;
    background-color: #bd2222;
    align-items: center;
    padding: 6px 18px;
    margin-top: 1px;
    font-family: "Poppins";
    font-size: 16px;
    font-weight: 500;
    color:#ffff;
    letter-spacing: 1.2px;
    margin-left: 10px;
    cursor: pointer;
  }
  .func_icon2 {
    font-size: 25px;
    font-weight: 800;
    color: #ffff;
  }
}
.video_funcs2:hover {
  background-color: #d81616;
}
.video-call-container-header {
  color: #ffff;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 18px;
  font-family: "Poppins";
}
.video-app-heading {
  margin-right: 10px;
}
.video-call-container-header-recording {
  font-size: 18px;
  margin-left: 10px;
  margin-right: 10px;
}
.video_consultation_main {
  padding: 20px;
  padding-bottom: 0;
  padding-top: 0px;
  display: flex;
  flex-direction: row;
  height: 82vh;
  .videoPlayers {
    position: relative;
    background-color: #151515;
    width: 100%;
    display: flex;
    justify-content: center;
  }

  .side_div_main {
    margin-left: 10px;
    width: 35%;
    height: 81.5vh;
    border-radius: 15px;
    box-shadow: #63636333 0px 2px 8px 0px;
    overflow: hidden;
  }
  #chat_toggle {
    display: none;
  }
  #participant_toggle {
    display: none;
  }
  .screen-share-video-player {
    width: 200%;
    height: 100%;
    .screenshare-video-content {
      width: 100%;
      height: 100%;
      position: relative;
      video {
        width: 100%;
        height: 100%;
        object-fit: fill;
        background: #101010;
      }
    }
    .screenshare-user-detail {
      position: absolute;
      display: flex;
      padding: 3px 8px;
      background-color: #3130308a;
      color: #ffffff;
      left: 12px;
      top: 12px;
      text-align: center;
      border-radius: 5px;
      width: auto;
    }
    .screenshare-user-icons {
      display: flex;
    }
  }
  .react-draggable-dragging {
    border: 2px solid #757575 !important;
  }
  .localVideoPlayer {
    min-width: 120px;
    position: absolute;
    width: 13%;
    max-width: 200px;
    border-radius: 5px;
    box-shadow: #0000001a 0px 4px 12px;
    border: 2px solid #151515;
    right: 20px;
    bottom: 10px;
    z-index: 9;
    overflow: hidden;
    background: #101010;
    margin: 10px;
    cursor: move;
    .local-video-content {
      display: flex;
      width: 100%;
      height: 100%;
      video {
        width: 100%;
        height: 100%;
        object-fit: fill;
        background: #101010;
      }
      .personBackGround {
        display: none;
        width: 100%;
        height: 100%;
        object-fit: fill;
        background: #101010;
        position: absolute;
      }
    }
  }
  .local-video-user-details {
    position: absolute;
    display: flex;
    padding: 1px 4px;
    background-color: #82828240;
    border: 1px solid #ffffff2e;
    color: #ffffff;
    left: 6px;
    bottom: 6px;
    text-align: center;
    border-radius: 5px;
    width: auto;
    .local-video-username {
      font-family: "Poppins";
      font-size: 0.7rem;
      font-weight: 300;
      padding: 2px 2px;
      letter-spacing: 1px;
    }
    .handrise-subicon-localvideo {
      font-size: 16px;
      cursor: pointer;
      margin-top: -3px;
    }
  }
  #players {
    display: flex;
    align-content: flex-start;
    flex-wrap: wrap;
    align-items: flex-start;
    justify-content: center;
    vertical-align: middle;
    border-radius: 20px;
    position: relative;
    background-color: #0f0f0fd9;
    width: 100%;
  }
  .antsdk-remote-video-players {
  }
  .antsdk-remote-video-data {
    position: relative;
    vertical-align: middle;
    align-self: center;
    border-radius: 5px;
    overflow: hidden;
    display: inline-block;
    box-shadow: 0px 12px 22px #00000066;
    background: #0f0f0f;

    .antsdk-remote-video-content {
      width: 100%;
      height: 100%;
      position: relative;
      video {
        width: 100%;
        height: 100%;
        object-fit: fill;
        background-color: #151515;
      }
      .antsdk-remote-video-speaker {
        position: absolute;
        width: 100%;
        height: 100%;
        border: 5px solid #06fa4f;
        display: none;
        top: 0;
        border-image-slice: 1;
        border-image-source: linear-gradient(to left, #00ff4c, #00b2df, #06fa4f);
        animation: border-flicker 1s linear infinite;
      }
      @keyframes border-flicker {
        0% {
          border-image-source: linear-gradient(to left, #00ff4c, #00b2df, #06fa4f);
        }
        50% {
          border-image-source: linear-gradient(#00ff4c, #00b2df, #06fa4f);
        }
        100% {
          border-image-source: linear-gradient(to right, #00ff4c, #00b2df #06fa4f);
        }
      }
      .antsdk-remote-video-user-details {
        position: absolute;
        display: flex;
        padding: 3px 8px;
        background-color: #3130308a;
        color: #ffffff;
        left: 12px;
        top: 12px;
        text-align: center;
        border-radius: 5px;
        width: auto;
        .antsdk-remote-video-username {
          font-family: "Poppins";
          font-size: 0.8rem;
          font-weight: 300;
          letter-spacing: 0.3px;
          max-width: 12ch;
          width: auto;
          text-overflow: ellipsis;
          overflow: hidden;
          white-space: nowrap;
          margin-top: 1px;
          padding: 1px 5px;
        }
        .antsdk-remote-video-mic-icon {
          align-items: center;
          display: flex;
          svg {
            width: 12px;
            height: 12px;
            margin-top: -3px;
          }
        }
        .antsdk-remote-video-handraise-icon {
          svg {
            width: 15px;
            height: 15px;
            margin-top: -6px;
            margin-left: 5px;
            animation: like 4s infinite;
            animation-fill-mode: forwards;
          }
        }
        .antsdk-remote-video-mic-icon-unmute {
        }
        .antsdk-remote-video-mic-icon-mute {
        }

        @keyframes like {
          0% {
            transform: scale(1);
          }
          1% {
            transform: scale(0.9);
          }
          20% {
            transform: scale(1);
          }
          35% {
            transform: scale(1.7);
          }
          40% {
            transform: scale(1);
          }
          50% {
            transform: scale(1.3);
          }
          65% {
            transform: scale(1);
          }
          90% {
            transform: scale(1);
          }
          100% {
            transform: scale(1);
          }
        }
      }
    }
  }
}

.ant-modal-content {
  background-color: #3d3d3d !important;
  border-radius: 20px;
}

.ant-modal-header {
  background-color: #535353 !important;
  border-radius: 12px;
  border-bottom: none;
  box-shadow: #32326926 0px 2px 5px 0px, #0000000d 0px 1px 1px 0px;
}
.ant-modal-title {
  color: #d1cfcf;
  font-size: 20px;
  font-family: "Poppins";
}

.joinModel {
  .modelLocalVideoPlayer {
    margin: 20px;
    width: 40%;
    video {
      width: 100%;
      height: 100%;
      object-fit: fill;
      border-radius: 10px;
      background: #151515;
    }
  }
  .joining-localVideoControls {
    .joining-form {
      margin-left: 20px;
    }
    .noUserName {
      box-shadow: 0 0 5px #f30101;
      &::placeholder {
        color: #f30101;
        opacity: 1;
        font-weight: 600;
      }
    }

    .joining-username {
      border-radius: 7px;
      width: 100%;
      border: none;
    }
    .join-label {
      font-size: 16px;
      font-weight: 450;
      color: #c0c0c0;
      letter-spacing: 1px;
      margin-top: 1px;
      margin-left: 3px;
    }
    .joinbtn {
      margin-right: 10px;
height:fit-content;
      padding: 7px 33px;
   
      border-radius: 7px;
      font-size: 18px;
      background-color: #37a991;
      border: none;
    }

    .cancelbtn {
      margin-right: 10px;
      padding: 7px 33px;
    height:fit-content;
      border-radius: 7px;
      font-size: 18px;
      border-color: #37a991;
      color: #37a991;
    }
  }

  .ant-switch-checked {
    background-color: #37a991;
  }
}

.InviteModel {
  .invite_radio {
    margin-top: 3px;
    vertical-align: top;
  }
  .ant-select:not(.ant-select-customize-input) .ant-select-selector {
    border-radius: 7px;
  }
  .invitation_label {
    font-size: 15px;
    font-family: "Poppins";
    color: #ffffff;
    font-weight: 400;
    margin-top: 10px;
  }
  .invitation_label_link {
    font-size: 14px;
    font-family: "Poppins";
    color: #e2e2e2;
    font-weight: 400;
    margin-top: 10px;
    opacity: 0.8;
  }
  .invite-user {
    width: 100%;
    border: none;
    border-radius: 7px;
    padding: 5px;
  }
  .invite-btn {
    width: 100%;
    padding-top: 10px;
    padding-bottom: 10px;
    background-color: #37a991;
    color: #ffffff;
    border: none !important;
    font-size: 19px;
    border-radius: 9px;
    font-weight: 600;
  }
}
.video-settings {
  .setting_label {
    color: #ffffff;
    font-family: "Poppins";
    font-size: 14px;
    font-weight: 450;
    margin-top: 20px;
  }
  .ant-select:not(.ant-select-customize-input) .ant-select-selector {
    border-radius: 7px;
  }
}
.options_container {
  width: auto;
  height: auto;
  padding: 10px;
  background-color: #0f0f0f;
  border-radius: 15px;

  .video_funcs_tooltip {
    border-radius: 10px;
    background: #0f0f0f;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    width: auto;
    padding: 10px 12px;
    cursor: pointer;
    position: relative;
    &:hover {
      background-color: #151515;
    }
  }
  .func_icon_tooltip {
    font-size: 20px;
    font-weight: 800;
    color: #ffffff;
  }
  .tooltip_options {
    font-size: 12.8px;
    font-family: "Poppins";
    font-weight: 400;
    padding-left: 10px;
    letter-spacing: 0.5px;
    color: #ffffff;
  }
  .handrise-icons {
    padding: 5px;
    background-color: #0f0f0f;
    border-radius: 7px;

    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }

  .handrise-subicon {
    padding: 7px;
  }
  .handrise-subicon:hover {
    background-color: #151515;
    border-radius: 7px;
    padding: 7px;
  }
}
.perview-local-video {
